.services {
  margin-bottom: 50px;
  border-bottom: 1px solid $border-color;

  @media (min-width: $screen-md) {
    padding-bottom: 50px;
  }

  $block-name:                &; // #{$block-name}__element

  & .tabs {

    &__content-item--notabs {
      display: block;
    }

    &__links {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-content: space-between;
    }

    &__link-wrap {
      width: 100%;
      padding: 0 20px;
      border: none;

      @media (min-width: $screen-md) {
        padding-left: 20px;
        padding-right: 30px;
      }

      @media (min-width: $screen-xl) {
        padding: 0 40px;
      }

      //&:not(:first-child) {

      .tabs__link {
        position: relative;

        &::after {
          display: block;
          content: "";
          position: absolute;
          right: -5px;
          top: 50%;
          transform: translateY(-50%);
          width: 5px;
          height: 10px;
          //background-color: currentColor;
          background: svg-load('../blocks/sprite-svg/svg/right-icon.svg', fill=currentColor);
          background-repeat: no-repeat;

          @media (min-width: $screen-md) {
            right: -10px;
          }
        }

        @media (min-width: $screen-md) {
          border-top: 1px solid rgba(0, 0, 0, 0.1);
        }
      }
      //}

      &--active {
        background-color: $white;
      }
    }

    &__link {
      padding: 5px 0;
      padding-right: 10px;
      font-size: 20px;
      font-weight: 700;
      text-decoration: none;
      color: $title-color;
      background: none;

      @media (min-width: $screen-md) {
        position: relative;
        padding: 10px 0;
        padding-right: 10px;
        font-size: 24px;
      }

      &:hover,
      &:focus {
        text-decoration: none;
      }
    }

    &__content-wrapper {
      padding: 20px 0 40px;
      border: none;

      @media (min-width: $screen-md) {
        padding-top: 0;
        padding-bottom: 0;
      }
    }

    &__content-item {

      &--active {

      }
    }
  }

  .dropdown {

    @media (min-width: $screen-md) {
      padding: 20px 0;
      background-color: $main-color;
    }

    @media (min-width: $screen-xl) {
      padding: 30px 0 40px;
    }
  }

  .dropdown-toggle {
    width: 100%;
    color: $title-color;
    background-color: $main-color;
    border-color: currentColor;

    @media (min-width: $screen-md) {
      display: none;
    }
  }

  .dropdown-menu {
    width: 100%;
    background-color: $main-color;

    @media (min-width: $screen-md) {
      display: block;
      position: static;
      float: none;
      padding: 0;
      margin: 0;
      border: none;
    }
  }

  &__col-left {
    @include col(xs, 12);
    @include col(md, 6);
    @include col(xl, 4);
    padding: 20px 15px;
    background-color: $main-color;

    @media (min-width: $screen-md) {
      padding-top: 0;
      background: none;
    }
  }

  &__col-right {
    @include col(xs, 12);
    @include col(md, 6);
    @include col-offset(xl, 1);
    padding: 0 15px;
  }

  &__dropdown-wrapper {

  }

  &__title {
    margin-top: 0;
    margin-bottom: 10px;

    @media (min-width: $screen-md) {
      position: static;
      overflow: none;
      clip: none;
      height: auto;
      width: auto;
      margin-bottom: 15px;
      padding: 0 20px;
      border: 0;
    }

    @media (min-width: $screen-xl) {
      padding: 0 40px;
    }

    &--first {
      margin-top: 40px;

      @media (min-width: $screen-md) {
        margin-top: 50px;
        margin-bottom: 50px;
        padding-left: 0;
      }

      @media (min-width: $screen-xl) {
        padding-left: 0;
      }
    }
  }

  &__img {
    margin-bottom: 40px;

    @media (min-width: $screen-xl) {
      margin-bottom: 50px;
    }

    img {
      @include object-fit();
    }
  }

  &__list {
    padding-left: 25px;
    list-style: none;
  }

  &__item {
    font-size: 18px;
    font-weight: 700;
    color: $second-color;

    &::before {
      display: inline-block;
      vertical-align: middle;
      content: "";
      width: 15px;
      height: 2px;
      margin-right: 10px;
      background-color: $second-color;
    }
  }

  &__btn-wrapper {
    margin-top: 20px;

    @media (min-width: $screen-md) {
      margin-top: 25px;
    }
    a {
      width: 100%;

      @media (min-width: $screen-md) {
        width: auto;
        min-width: 160px;
      }

      @media (min-width: $screen-xl) {
        min-width: 185px;
      }
    }
  }
}
